* {
    box-sizing: border-box;
  }

body {
    margin : 0px;
    padding : 0px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
#potato,
.container {
    height : 100%;
    display: flex;
    justify-content: center;
}

.loader { /*App.js : isLoading이 true일 때*/
    width : 100%;
    height : 100%;
    display: flex;
    justify-content: center;
    align-items: center; /*로딩 문구 화면 가운데로*/
    font-weight: 300;
}

.movies { /*App.js : isLoading이 false일 때*/
    display: flex;
    justify-content: space-between; /* 각 영화 사이 여백*/
    flex-wrap: wrap; /*이게 있으니까 한 줄에 두 개씩 나옴*/
    padding : 50px;
    padding-top : 70px;
    width : 80%;
    align-items: flex-start; /* 글자 끝나는 위치 맞춰서 상자 끝남 */
}

.movies_movie { /*Movie.js : Movie 컴포넌트*/
    width : 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; 
    font-weight: 300; /* 글자 두께 얇아짐 */
    padding : 20px;
    color: #b2b3bc;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),  0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies img { /*Movie.js : poster*/
    position : relative;
    top : -50px; /* 상자 밖으로 50px 올라감*/
    max-width: 120px;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),  0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie_title, .movie_year { /*Movie.js : title, year*/
    font-weight: 300; /* 폰트 두께 */
    margin: 0px;
}

.movie_title {
    margin-bottom: 10px;
    font-size: 24px;
    color: #2c2c2c;

}

.movie_year, .movie_genres {
    color: #b2b3bc;
}

.movie_genres {
    list-style: none; /* ul 앞에 점 없애줌 */
    padding : 0; /* 테두리랑 딱 붙게 */
    margin: 5px; 
    display: flex;
    font-size: 10px;
}

.movie_genres li {
    margin-right: 10px; /* 장르 사이 공백 */
}